<script>
import * as d3 from 'd3';
import * as _ from 'underscore';
import {mapState, mapGetters} from 'vuex';

export default {
    name: 'plot',
    props: [],
    computed: {
        ...mapState(['selectedMuseum']),
        ...mapGetters(['getMuseumData'])
    },
    data: () => ({ 

    }),
    watch: {
        getMuseumData() {
                    this.render();

        },
        // getMuseumData: {
        //     handler: function (data) {
               
        //             // console.log("watch dataset in slopechart", data)
        //             this.render();
                
        //     },
        //         deep: true
        //     }
    },
    mounted() {
        if(this.getMuseumData && this.getMuseumData.length) this.render()
        // setTimeout(() => {this.render()}, 1000)
    },    
    methods: {
// space between axis and viz -> group by data and use the max length to determine

        formatDataset: function() {
            let data = [...this.getMuseumData];

        },
        fillMissingYears(dates_array) {

            let bce = [], ad_mid = [], ad_latest = [];

            let start = d3.min(dates_array);
                // start = start < 0 ? start : 0;
            
            let end = d3.max(dates_array);

            start < 0 ? start -= 200 : start -= 10;
            end += 20;
        
    
            while(end > start) {
               if(start < 0) {
                    bce.push(start);
                    start +=  start < 0 ? 500 : 10;

               } else if(start <= 1580) {                    
                    ad_mid.push(start);
                    // if(ad_mid.indexOf(0) == -1) ad_mid.push(0);
                    start += 100;
               } else {
                    ad_latest.push(start);
                    start += 10;                    
               }
            }      
            return [bce, ad_mid, ad_latest];
        },        
        // fillMissingYears(dates_array) {

        //     let fill_dates = [];

        //     let start = d3.min(dates_array);
                
            
        //     let end = d3.max(dates_array);

        //     if(start < 0) {
        //         start -= 10;
        //     } else {
        //         start -= 10;
        //     }

        //     while(end > start) {
        //        if(start < 0) {
        //             start += 1000
        //             fill_dates.push(start)
        //        } else if(start === 0) {
        //             start += 10
        //             console.log(start)

        //             fill_dates.push(start)
        //        } else {
        //             start += 10
        //             fill_dates.push(start)                   
        //        }
        //     }      
        //     return fill_dates;
        // },
        render: function() {

            // followed this example: 
            console.log("Draw beeswarm");

            d3.selectAll("#viz svg").remove();

            // const {data, dates} = this.formatDataset();


            const data = [...this.getMuseumData];
            let years = _.chain(data).pluck('date').pluck('label').uniq().sortBy().value()

            years = this.fillMissingYears(years);

console.log(years)

            _.each(years, year => {
                    let thisData = _.filter(data, d => {
                        let date = d.date.label;
                        return date >= year[0] && date <= year[year.length-1]
                    })

            // console.log(year, thisData)
                    if(thisData.length) {
                         this.drawTimeline(year, thisData.length * 12, thisData.length / 10, thisData);
                    }

              
            });

            // this.drawTimeline(years, 6500, thisData);



        },
        drawTimeline: function(years, thisHeight, nTicks, data) {

            let margin = ({top: 100, right: 100, bottom: 30, left: 100}),
                height = thisHeight,
                width = 800,
                formatTime =  d3.timeParse("%Q");
          
            const svgW = width + margin.left + margin.right;
            const svgH = height + margin.top + margin.bottom;

            const svg = d3.select("#viz")
                .append("svg")
                .attr("preserveAspectRatio", "xMinYMin meet")
                .attr("viewBox", `0 0 ${svgW} ${svgH}`)
                .classed("svg-content", true)
                .append("g")
                    .attr("transform",
                        "translate(" + margin.left + "," + margin.top + ")");
            
            const minMaxYear = d3.extent(years, d => d).reverse();

            const yScale = d3.scaleLinear()
                .domain(minMaxYear)
                .range([height, 0]);
            // const rScale = d3.scaleSqrt().domain([0, max_pop]).range([0, 8])

            svg.append("g")
                .call(d3.axisLeft(yScale)
                    .ticks(nTicks)
                    .tickFormat((d, i) => d < 0 ? `${-d} BCE` :  `${d} AD`)
                )
                .style("transform", `translateX(${width / 2}px`)
  

            const force = d3.forceSimulation(data)
                    .force('forceY', d3.forceY(d => yScale(d.dateClean)).strength(1))
                    .force('forceX', d3.forceX(width/2 + 110).strength(0.9))
                    .force('collide', d3.forceCollide(12))
                   
                    .stop();

            const NUM_ITERATIONS = 1200;
            
            for (let i = 0; i < NUM_ITERATIONS; ++i) {
                force.tick();
            }
            
            force.stop();
            let minLabel = minMaxYear[1] < 0 ? `${-minMaxYear[1]} BCE` : `${minMaxYear[1]} AD`,
                maxLabel = minMaxYear[0] < 0 ? `${-minMaxYear[0]} BCE` : `${minMaxYear[0]} AD`;
            let theseYearsLabel = `Artifacts on exhibit between ${minLabel} and ${maxLabel}`;

                svg.append('text')
                    .attr("x",  width / theseYearsLabel.length * 7)
                    .attr("y", -60)
                    .style("font-size", "16pt")
                    .style("font-weight", 300)
                    .text(theseYearsLabel.toUpperCase())
    
            let nodes = svg.selectAll("circle")
                .data(data)
                .enter()
                .append("circle")
                .attr("cx", d => d.x)
                .attr("cy", d => d.y)
                .attr("r", d => 12)
                .style("fill", "#bdbdbd")
                .style("opacity", 0.8);
            
                // nodes.append("svg:image")
                //     .attr("xlink:href", "data/favicon.ico")
                //     .attr("x", d => d.x)
                //     .attr("y", d => d.y)
                //     .attr("width", 10)
                //     .attr("height", 10);

        } 
    }
}

</script>
